@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@500&family=Montserrat&family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}a,a:hover,a:focus,a:active{text-decoration:none;color:inherit}#projectDescription,section{display:-webkit-inline-flex;display:inline-flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column}.buttonBox,.subButtonBox,#headDesc{display:-webkit-inline-flex;display:inline-flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}#footer,.each-slide{display:-webkit-inline-flex;display:inline-flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}#pageButtonBox,#header{display:-webkit-inline-flex;display:inline-flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center}*{box-sizing:border-box}#root,.App{min-width:100vw}#root{background-color:#f4e9cd}.display{display:inline-block}.hide{display:none}h1,h2,h3,h4{text-align:center}h2,h3,h4{font-family:"Montserrat",sans-serif}h1{font-family:"Cormorant Garamond",serif;font-size:4.5em;border:solid 10px #581c5e;color:purple;width:50vw;text-align:center;margin:auto}h2{font-size:2.5em;margin-bottom:30px}h3{font-size:2em}h4{font-size:1.5em;margin:0px 20px 0px 20px}a{padding:4px}header>a:hover,section>a:hover{background-color:#1e2019;border-bottom:solid #f4e9cd;border-top:solid #f4e9cd}section{padding:50px;width:100vw;max-width:100vw}article{font-family:"Playfair Display",serif;font-size:1.1em;line-height:2;max-width:900px}button{border:none;background-color:#41818b;color:#f4e9cd;border-radius:5px;font-family:"Montserrat",sans-serif;font-size:1.2em}button:disabled{opacity:.5}button:enabled:hover{background-color:#581c5e}#header,#headerPlaceholder{position:-webkit-sticky;position:sticky;top:0px;min-width:100vw;transition:all .3s;font-family:"Montserrat",sans-serif}.normal{height:60px;font-size:1.5em;color:#581c5e;background-color:#f4e9cd}.stickyShrink{height:45px;background-color:#581c5e;z-index:1;color:#f4e9cd;font-size:1.2em}.stickyShrink .highlight{border-top:solid #f4e9cd;border-bottom:solid #f4e9cd}#home>*{color:#1e2019}#headshot{max-width:300px}#headDesc{margin-top:50px}#homeDescription{font-family:"Playfair Display",serif;width:400px;margin:30px}#downloadLink{font-size:1.5em}#contactInfo{background-color:#aa5042}#contactInfo>*{color:#f4e9cd}#aboutMe{background-color:#41818b}#aboutMe>*{color:#f4e9cd}#resume{background-color:#aa5042}#pageIndicator,#downloadLink{font-size:1.6em;font-family:"Cormorant Garamond",serif;margin-bottom:20px}#pageButtonBox{margin-top:30px;width:90%}.pageButton{width:100px;height:30px;transition:all 1s}#resume>*{color:#f4e9cd}#resumeHeader{color:#f4e9cd}#portfolioProject{background-color:#1e2019;display:inline-block}#portfolioProject>*{color:#f4e9cd}.each-slide{height:calc(100vh - (50px * 2));width:100%}.each-slide>img{object-fit:contain;max-height:100%;max-width:100%}.react-slideshow-container+ul.indicators .each-slideshow-indicator:before{background:#f4e9cd}.buttonBox,.subButtonBox{width:100%}.buttonBox{margin:20px 0px 20px 0px}.subButtonBox{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:auto}.subButtonBox>*{transition:all 1s}#projectTitle{color:#f4e9cd}.portfolioButtons{margin:auto;fill:#f4e9cd;width:50px}#leftButton{-webkit-transform:rotate(180deg);transform:rotate(180deg)}#projectDescription{width:100%}#technologyList{text-align:center;font-size:1.6}.projectSubheader{margin:30px 0px 30px 0px}.fromRight{animation:swipeR 1s;-webkit-animation:swipeR 1s;-moz-animation:swipeR 1s;-o-animation:swipeR 1s;-ms-animation:swipeR 1s}@keyframes swipeR{0%{-webkit-transform:translateX(100vw);transform:translateX(100vw)}100%{-webkit-transform:translateX(0vw);transform:translateX(0vw)}}@-webkit-keyframes swipeR{0%{-webkit-transform:translateX(100vw);transform:translateX(100vw)}100%{-webkit-transform:translateX(0vw);transform:translateX(0vw)}}.fromLeft{animation:swipeL 1s;-webkit-animation:swipeL 1s;-moz-animation:swipeL 1s;-o-animation:swipeL 1s;-ms-animation:swipeL 1s}@keyframes swipeL{0%{-webkit-transform:translateX(-100vw);transform:translateX(-100vw)}100%{-webkit-transform:translateX(0vw);transform:translateX(0vw)}}@-webkit-keyframes swipeL{0%{-webkit-transform:translateX(-100vw);transform:translateX(-100vw)}100%{-webkit-transform:translateX(0vw);transform:translateX(0vw)}}#footer{min-width:100vw;height:60px;border-top:solid 5px #581c5e;border-bottom:solid 5px #581c5e}.socialIcons{margin:0px 10px 0px 10px}
